.pinContainer {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    background-color: #00000050;
    animation: moveUpDown 2s ease-in-out infinite;
    cursor: pointer;
}

.pinContainer:hover {
    background-color: #000000A0;
}

@keyframes moveUpDown {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-20px);
    }
}


@keyframes moveUp {
    0% {
        transform: translateY(200px);
    }
    100% {
        transform: translateY(0px);
    }
}

.info {
    border: 2px solid #fff;
    cursor: pointer;
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.infoButton {
    color: #fff;
    border-radius: 50%;
    background-color: #00000050;
    display: flex;
    justify-content: center;
    align-items: center;
}

.infoButton:hover {
    background-color: #000000A0;
}

.infoPulse {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    animation: pulse 2s infinite;
    z-index: -1;
    border-radius: 50%;
}

@keyframes pulse {
    0% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 0.7;
    }
    100% {
        transform: translate(-50%, -50%) scale(1.2);
        opacity: 0;
    }
}

.previewBlock {
    animation: moveUp 1s ease-in-out;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 70px;
}

.previewContainer {
    background-color: #000000a0;
    gap: 10px;
    margin-bottom: 10px;
    overflow-x: auto;
}

.centerContent {
    display: flex;
    justify-content: center;
}

.preview {
    border: white 1px solid;
    width: 80px;
    height: 80px;
    cursor: pointer;
}

.arrowContainer {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    cursor: pointer;
    background-color: #00000050;
}

.arrowContainer:hover {
    background-color: #000000A0;
}

.upsideDown {
    transform: scaleY(-1);
}

.infoBlock {
    position: absolute;
    top: 20px;
    left: 20px;
    background-color: #000000A0;
    border: white 1px solid;
    border-radius: 10px;
    color: white;
    padding: 10px;
    max-width: 400px;

    transform: translateX(-150%);
    transition: transform 0.4s ease-in-out;
}

.infoBlock.visible {
    transform: translateX(0);
}

.closeButton {
    position: absolute;
    cursor: pointer;
    background-color: #00000050;
    border-radius: 50%;
    width: 30px;
    height: 30px;
}
.closeButton:hover {
    background-color: #000000A0;
}


.overlayImage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 1s ease-in-out;
    pointer-events: none;
    z-index: 20000000;
};

.welcomeBlock {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 90%;
    max-width: 400px;
    transform: translate(-50%, -50%);
    background-color: #000000a0;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    z-index: 20000001;

    & button {
        background-color: #000000A0;
        border: #7851a9 3px solid;
        border-radius: 10px;
        font-size: 14pt;
        padding: 12px 29px;
        cursor: pointer;
        margin: 10px;
        color: white;
    }
    & button:hover {
        background-color: #7851a9A0;
    }

    & h1 {
        text-align: center;
    }
}