.button {
    border-radius: 50%;
    cursor: pointer;
    border: white solid 1px;
    height: 50px;
    width: 50px;
    background-color: #00000050;
    margin: 10px;
}

.button:hover {
    background-color: #000000A0;
}

.buttonsContainer {
    position: fixed;
    top: 0;
    right: 0;
}