.mapContainer {
    position: relative;
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
    border: 3px solid #fff;
    overflow: hidden;
}

.mapImage {
    display: block;
    width: 100%;
    height: auto;
}

.mapPoint {
    position: absolute;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    background-color: #ff4136;
    border: 2px solid white;
    border-radius: 50%;
    cursor: pointer;
    padding: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    transition: transform 0.2s ease;
}

.mapPoint:hover {
    transform: translate(-50%, -50%) scale(1.2);
    z-index: 10;
}

.mapPoint.disabled {
    background-color: #6a1e1e;
}

.mapPointPulse {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #ff4136;
    animation: pulse 2s infinite;
    z-index: -1;
}

@keyframes pulse {
    0% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 0.7;
    }
    100% {
        transform: translate(-50%, -50%) scale(2.5);
        opacity: 0;
    }
}

.mapOverlay {
    position: fixed;
    top: 0;
    right: 0;
    width: 90%;
    max-width: 500px;
    box-shadow: -10px 0 25px rgba(0, 0, 0, 0.15);
    z-index: 20000000;
    transform: translateX(100%);

    transition: transform 0.4s ease-in-out;
}

.mapOverlay.visible {
    transform: translateX(0);
}